import React, {
  ChangeEvent,
  Fragment,
  useCallback,
  useMemo,
  useState,
} from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import TextField from '@mui/material/TextField';
import {graphql, PageProps, Link} from 'gatsby';

import Layout from '../../components/layout';
import {createSlug} from '../../utils/CommonUtils';

export type BranchProps = {
  id: string;
  name: string;
  code: string;
};

type DataProps = {
  allStrapiBranche: {
    nodes: BranchProps[];
  };
};

// markup
const Branches = ({data: {allStrapiBranche}}: PageProps<DataProps>) => {
  const [searchText, setSearchText] = useState('');

  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setSearchText(event.target.value);
    },
    [setSearchText]
  );

  const filteredBranches = useMemo(() => {
    // filter branch by name or code
    return allStrapiBranche.nodes.filter((branch) => {
      return (
        branch.name.toLowerCase().includes(searchText.toLowerCase()) ||
        branch.code.toString().includes(searchText)
      );
    });
  }, [allStrapiBranche.nodes, searchText]);

  // console.log(filteredBranches)

  return (
    <Layout headerTitle="Branches">
      <Box>
        <TextField id="standard-search"
          label="Search Branch Name"
          type="search"
          variant="filled"
          onChange={handleChange}
          fullWidth/>
      </Box>
      <List>
        {filteredBranches.map((node) => {
          const branchSlug = createSlug(node.name);
          return (
            <Fragment key={node.id}>
              <ListItem disablePadding>
                <ListItemButton component={Link} to={`/branches/${branchSlug}`}>
                  <ListItemText primary={node.name} />
                </ListItemButton>
              </ListItem>
              <Divider />
            </Fragment>
          );
        })}
      </List>
    </Layout>
  );
};

export const query = graphql`
  query {
    allStrapiBranche(sort: {name: ASC}) {
      nodes {
        id
        name
        code
      }
    }
  }
`;

export default Branches;
